
.gallery-container {
  transition: margin-top 0.5s; /* Add smooth transition effect */
}

.gallery-container.fullscreen {
  margin-top: 0; /* Adjust the margin or other styles as needed */

  
}

/* Ensure the gallery takes up the entire width of the parent container */
.gallery-container.fullscreen .image-gallery {
  width: 100%;
  max-width: 100%;
}

.gallery-container.fullscreen .image-gallery .image-gallery-content {
 height: 100%;
}

 .image-gallery .image-gallery-content .image-gallery-slide-wrapper {
  height: 400px;
}

.gallery-container.fullscreen .image-gallery .image-gallery-content .image-gallery-slide-wrapper {
  height: calc(100% - 104px) !important;
}

 .image-gallery .image-gallery-content .image-gallery-slide-wrapper .image-gallery-swipe {
  height: 100%;
  width: 100%;
}

.image-gallery-slides {
  height: 100% !important;
  width: 100% !important;
}

.gallery-container.fullscreen .image-gallery-slide.image-gallery-center {
  height: 100%;
}

.gallery-container.fullscreen .image-gallery .image-gallery-content .image-gallery-slide .image-gallery-image {
  max-height: 100% !important;
  object-fit: cover;
  padding: 0;
  height: 100%;
}

/* Adjust the styles as needed to handle the Bootstrap col-8 container */
@media (min-width: 768px) {
  .col-8 .gallery-container.fullscreen {
    width: 100vw;
    position: fixed;
    z-index: 1000;
    background-color: #fff; /* Set background color as needed */
  }
}

.image-gallery .image-gallery-content .image-gallery-slide .image-gallery-image {
  max-height: 100% !important;
  object-fit: unset;
  width: 100%;
}

.image-gallery-slide {
  height: 100%;
}